	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Josh Triplett\n\nWe've ported Python to run directly on hardware, without an OS, as a testing and exploration environment for firmware, ACPI, and UEFI. This talk will explore porting Python to a new platform, embedding Python, recreating enough of libc and POSIX to run Python without an OS, and binding to platform-specific services. Includes live demo of bare-metal Python, directly driving hardware.\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT32M57S"},"presenter":{"name":null},"slug":"josh-triplett-porting-python-to-run-without-an-os-pycon-2015","source":{"id":"bYQ_lq5dcvM","provider":"youtube"},"tag":[],"title":"Josh Triplett - Porting Python to run without an OS - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"josh-triplett-porting-python-to-run-without-an-os-pycon-2015","tags":[]}}

